export const getMess = (user) =>
  fetch(`https://astrange-server.herokuapp.com/messages/${user}`)
    .then((res) => {
      if (res.ok) {
        return res.json();
      }
    })
    .then((jsonRes) => jsonRes);

export const getPosts = () =>
  fetch("https://astrange-server.herokuapp.com/posts")
    .then((res) => {
      if (res.ok) {
        return res.json();
      }
    })
    .then((jsonRes) => jsonRes);
